Color
*/
/*font*/
@media (min-width: 576px) {
  body {
    font-size: 1.5rem; }
    body .queries-sm {
      display: block;
      font-size: 1rem; }
    body .queries-xsm {
      display: none; }
    body header {
      flex-direction: row;
      justify-content: space-between; }
      body header nav {
        width: 100%; }
        body header nav .logo {
          margin-left: 10%; }
      body header button {
        align-self: baseline;
        margin-right: 2%; }
  #first-section article {
    font-size: 0.8rem;
    left: 10%;
    top: 25%; }
    #first-section article img {
      width: 30vw;
      margin-bottom: 7%; }
    #first-section article button {
      margin-top: 4%;
      padding: 4% 15%; }
  #first-section .adresses {
    bottom: 0%; }
  #second-section article div {
    right: -50%;
    top: 100%; }
  #fourth-section button {
    padding: 4% 30%;
    background-color: #8a962b;
    border: none;
    color: #f9f9f9;
    text-transform: uppercase;
    font-size: 1rem;
    min-width: 250px; } }

@media (min-width: 768px) {
  /* sticky nav
       sliders
    /* phone at first section and none footer*/
  body {
    font-size: 1.3rem; }
    body .queries-md {
      display: block;
      font-size: 1rem; }
    body .queries-xsm,
    body .queries-sm {
      display: none; }
  header nav .logo {
    width: 15vw; }
  #first-section {
    margin-top: 0%; }
    #first-section article {
      top: 30%;
      left: 15%; }
    #first-section .adresses {
      right: 0px;
      bottom: 5%;
      color: #f9f9f9;
      text-shadow: none; }
  #second-section {
    margin: 0% 10%;
    font-size: 1rem;
    overflow: visible; }
    #second-section article {
      top: -0%;
      left: 3%;
      position: relative; }
    #second-section .woman-in-room {
      position: absolute;
      left: -6%;
      width: 71%; }
    #second-section .litle-pic {
      width: 53%;
      right: -3%;
      height: 29%;
      top: 32%;
      object-fit: cover;
      object-position: center; }
  #third-section {
    overflow: visible;
    margin-top: -10%; }
    #third-section .room-picture {
      left: 50%;
      top: 5%;
      width: 38%;
      height: 107%;
      object-fit: cover;
      object-position: revert; }
    #third-section .chill-picture {
      width: 53%;
      top: 45%; }
    #third-section article {
      left: -35%;
      top: 5%; }
      #third-section article h2 {
        text-align: center; }
      #third-section article h3,
      #third-section article h4,
      #third-section article h5 {
        text-align: center;
        font-size: 1rem;
        line-height: 0;
        margin: 10px; }
      #third-section article div {
        top: 100%;
        left: 32%;
        color: #000000;
        text-shadow: none;
        text-align: center;
        font-size: 1rem; }
  footer .logo {
    margin-left: auto;
    width: 15vw; } }

@media (min-width: 992px) {
  body {
    font-size: 1rem; }
    body .queries-lg {
      display: block; }
    body .queries-xsm,
    body .queries-sm,
    body .queries-md {
      display: none; }
  header {
    height: 8vw;
    position: fixed;
    top: 0;
    z-index: 2;
    width: 100%;
    background-color: #f9f9f9; }
    header nav .logo {
      width: 10vw; }
  #fourth-section {
    background-color: #f9f9f9;
    align-self: center;
    display: flex; }
    #fourth-section section img {
      width: 75%;
      position: sticky;
      top: 4%;
      left: 12.5%;
      margin: auto; }
    #fourth-section section main {
      display: flex;
      align-items: center;
      padding: 5% 5% 5% 5%;
      margin-bottom: 5%;
      margin-left: 45px;
      margin-right: 45px;
      box-shadow: 0px -3px 20px #00000029; }
      #fourth-section section main h2 {
        height: 50px; }
      #fourth-section section main article {
        display: flex;
        justify-content: space-between;
        width: 100%; }
        #fourth-section section main article button {
          margin-top: 50px; }
    #fourth-section .card-description {
      min-height: 100px;
      font-size: 1.2rem; }
    #fourth-section .card-size {
      height: 115px; } }

@media (min-width: 1200px) {
  body {
    font-size: 1.5rem; }
    body .queries-xsm,
    body .queries-sm,
    body .queries-md,
    body .queries-lg {
      display: none; }
    body .queries-xl {
      display: block; } }
